.user-path-map {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: -40vh;
    left: 0;
    z-index: 20000;
    width: 100%;
    padding: 48px 0;
    background: rgba(255, 255, 255, 0.5);
    transition: 0.5s ease;
}

.show-user-path-map {
    top: 0;
}
.hide-user-path-map {
    top: -40vh;
}