.floor-elevator-container {
    display: flex;
    flex-direction: column;
    gap: 2px;
    position: absolute;
    bottom: 72px;
    left: 104px;
    .floor-elevator {
        display: flex;
        position: relative;
        &:nth-child(1) {
            .floor-icon {
                height: 40px;
            }
        }
        &:nth-child(4),
        &:nth-child(6),
        &:nth-child(8) {
            .floor-icon {
                height: 26px;
            }
        }
        &:nth-child(5) {
            .floor-icon {
                height: 124px;
            }
        }
        &:nth-child(8) {
            margin-bottom: 3px;
            &:after {
                position: absolute;
                display: block;
                content: "";
                top: 28px;
                left: -9px;
                height: 1px;
                width: 110px;
                background: #fff;
            }
        }
        .floor-icon {
            width: 92px;
            height: 12px;
            background: rgba(255, 255, 255, 0.5);
            transition: 0.3s ease;
            border: 1px solid $cl-white;
            cursor: pointer;
            &:hover {
                background: rgba(255, 255, 255, 0.7);
            }
        }
        .number {
            position: absolute;
            top: calc(50% - 9px);
            left: 92px;
            width: max-content;
            opacity: 0;
            transition: 0.3s ease;
            color: $cl-gray01;
        }
        .highlight {
            background: $cl-white;
        }
        &:hover {
            .number {
                left: 104px;
                opacity: 1;
            }
        }
    }
}