.section-information {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;

    .back {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 76px;
        left: 52px;
        width: 48px;
        height: 48px;
        background: $cl-gray02;
        border-radius: 50%;
        transition: 0.3s ease;
        cursor: pointer;
        img {
            width: 32px;
            height: 32px;
        }
        &:hover {
            left: 50px;
            background: $cl-gray01;
        }
        
    }

    .floor-container {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: absolute;
        top: 0;
        left: calc(50% - 200px);
        height: 100vh;
        padding: 60px 0;

        .floors {
            display: flex;
            align-items: center;
            gap: 200px;

            .floor-number {
                width: 60px;
                p {
                    color: $cl-gray04;
                }
            }

            .floor-pin {
                display: flex;
                align-items: center;
                transition: 0.3s ease;
                opacity: 0.6;
                .pin-head {
                    width: 8px;
                    height: 8px;
                    border-radius: 50%;
                    background: $cl-black;
                }
                .pin-line {
                    width: 140px;
                    height: 1px;
                    background: $cl-black;
                    border: none;
                    transition: 0.3s ease;
                }
                .pin-icon {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 32px;
                    height: 32px;
                    border-radius: 50%;
                    border: 1px solid $cl-black;
                    img {
                        width: 24px;
                        height: 24px;
                    }
                }
                .pin-title {
                    padding: 8px;
                }

                &:hover {
                    opacity: 1;
                    .pin-line {
                        width: 148px;
                    }
                }
            }
        }
    }
}

.fade-in {
    opacity: 1;
    transition: 0.3s;
    z-index: 100;
}
.fade-out {
    opacity: 0;
    transition: 0.3s;
    z-index: -100;
}

