.floor-change-controller {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 2px;
    position: absolute;
    left: 48px;
    bottom: 72px;
    width: auto;
    height: 337px;
    .button {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 36px;
        height: 36px;
        transition: 0.3s ease;
        opacity: 0.6;
        cursor: pointer;
        &:hover {
            opacity: 1;
        }
        img {
            width: 24px;
            height: 24px;
        }
    }
    .button-up {
        &:hover {
            top: -4px;
        }
    }
    .button-down {
        &:hover {
            top: 52px;
        }
    }
}