.floor-popup {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 20000000;
    width: 0;
    height: 100vh;
    background: $cl-black;
    transition: 0.5s ease-in-out;
    opacity: 0;
    overflow: hidden;
    .button-close {
        position: absolute;
        right: 48px;
        top: 42px;
        transition: 0.3s ease;
        cursor: pointer;
        &:hover {
            opacity: 0.5;
        }
    }
    .popup-header {
        display: flex;
        align-items: center;
        padding: 48px 42px;
        .popup-title {
            display: flex;
            align-items: center;
            gap: 4px;
            h2 {
                color: $cl-white;
            }
        }
        .popup-content-navigation {
            ul {
                display: flex;
                margin-left: 24px;
                gap: 12px;
                li {
                    cursor: pointer;
                    transition: 0.3s ease;
                    color: $cl-white;
                    &:hover {
                        opacity: 0.5;
                    }
                }
            }
        }
    }
    .popup-text-content {
        display: flex;
        flex-direction: column;
        gap: 12px;
        width: 25%;
        padding: 48px 42px;
    }
    .popup-image-content {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        width: 100%;
        height: 100vh;
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
}

.fade-in-right {
    width: 100%;
    opacity: 1;
}