.facilities {
    z-index: 10;
    transition: 0.3s ease;
    .facility-pin{
        display: flex;
        align-items: center;
        gap: 12px;
        position: absolute;
        width: max-content;
        cursor: pointer;
        .facility-icon {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 42px;
            height: 42px;
            border: 1px solid $cl-black;
            border-radius: 50%;
            img {
                width: 28px;
                height: 28px;
            }
        }
        .facility-title {
            transition: 0.3s ease-in-out;
            opacity: 1;
        }
    }
    .f18-20-facility-pin-1 {top: -100px;  left: -100px;}
    .f18-20-facility-pin-2 {top: 100px;  left: 100px;}
    .f17-facility-pin-1 {}
    .f16-facility-pin-1 {}
    .f14-15-facility-pin-1 {}
    .f5-13-facility-pin-1 {}
    .f3-4-facility-pin-1 {}
    .f2-facility-pin-1 {}
    .b1-f1-facility-pin-1 {top: 100px;  left: 100px;}
    .b1-f1-facility-pin-2 {top: -100px;  left: -100px;}
    .b2-facility-pin-1 {}
    .b3-facility-pin-1 {}
    .b4-facility-pin-1 {}
    
    .visible {
        opacity: 1;
        transition: 0.5s ease;
        transition-delay: 0.5s;
    }
    
    .unvisible {
        opacity: 0;
        transition: 0.1s ease;
    }
}