.floor-information {
    position: relative;
    width: 50%;
    height: 100%;
    padding: 72px 104px;
    transition: 0.3s ease;
    .button-pre {
        position: absolute;
        top: 86px;
        left: 32px;
        width: 48px;
        height: 48px;
        transition: 0.3s ease;
        &:hover {
            left: 28px;
        }
    }
    .floor-information-container {
        display: flex;
        flex-direction: column;
        position: absolute;
        z-index: -10;
        gap: 18px;
        width: 304px;
        opacity: 0;
        .floor-number {
            position: relative;
            height: 84px;
            .rolling-title {
                position: absolute;
                top: 12px;
                transition: 0.4s ease-in-out;
                opacity: 0;
            }
            .rolling {
                top: 0;
                opacity: 1;
            }
        }
        .floor-spec {
            display: flex;
            gap: 24px;
        }
    }
    .left-slide-hidden {
        opacity: 0;
        z-index: -10;
    }
    
    .left-slide {
        z-index: 1;
        opacity: 1;
    }
}

.hide {
    opacity: 0;
}

.show {
    opacity: 1;
}