$cl-black : #000000;
$cl-gray05 : #333333;
$cl-gray04 : #626262;
$cl-gray03 : #8F8F8F;
$cl-gray02 : #C8C8C8;
$cl-gray01 : #E4E4E4;
$cl-white : #FFFFFF;

$cl-main : #F7EEDF;

@font-face {
    font-family: Helvetica-Bold;
    src: url("./assets/font/HelveticaNeueLTPro-BdCn.otf") format("opentype");
}

@font-face {
    font-family: Helvetica-Regular;
    src: url("./assets/font/HelveticaNeueLTPro-Md.otf") format("opentype");
}


@mixin heading-font {
    font-family: Helvetica-Bold, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
@mixin body-font {
    font-family: Helvetica-Regular, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}


@mixin title {
    font-size: 72px;
    line-height: 84px;
    @include heading-font;
}
@mixin heading-large {
    font-size: 48px;
    line-height: 56px;
    @include heading-font;
}
@mixin heading-medium {
    font-size: 36px;
    line-height: 42px;
    @include heading-font;
}
@mixin heading-small {
    font-size: 24px;
    line-height: 28px;
    @include heading-font;
}
@mixin sub-heading {
    font-size: 18px;
    line-height: 22px;
    @include heading-font;
}



@mixin body-large {
    font-size: 16px;
    line-height: 22px;
    @include body-font;
}
@mixin body-medium {
    font-size: 14px;
    line-height: 16px;
    @include body-font;
}
@mixin body-small {
    font-size: 12px;
    line-height: 14px;
    @include body-font;
}
@mixin micro {
    font-size: 8px;
    line-height: 10px;
    @include body-font;
}